import React from "react";
import { useSearchParams } from "react-router-dom";
import styled from "styled-components";
import useAxios from "../api/Axios";
import { useDispatch, useSelector } from "react-redux";
import { reset } from "../reduxjs/ReloadSlice";

const TableForMark = React.lazy(() =>
  import("../components/assets/MarksEntry/TableForMarks")
);
const TopInformationPanel = React.lazy(() =>
  import("../components/assets/TopInformationPanel")
);

const Class = () => {
  const [Params] = useSearchParams();
  const axios = useAxios();
  const dispatch = useDispatch();

  const [classInfo, setClassInfo] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  const { flag } = useSelector((state) => state.reload);

  React.useEffect(() => {
    getClass();
    if (flag) {
      dispatch(reset());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [Params, axios, flag, dispatch]);

  const getClass = async () => {
    try {
      const url = "/view?id=" + Params.get("id");
      await axios.get(url).then((res) => {
        setClassInfo(res.data);
      });
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  return (
    <Section>
      {loading ? (
        "Loading..."
      ) : (
        <Container>
          {/*
           * Course Name
           * Semester
           * Session
           * Teacher Name
           */}
          <TopInformationPanel classInfo={classInfo} onReloadPage={getClass} />
          <BodyPanel>
            {/* * Marks Table View */}
            <TableForMark classInfo={classInfo} />
          </BodyPanel>
        </Container>
      )}
    </Section>
  );
};

export default Class;

export const Section = styled.section`
  height: 95vh;
  margin-top: 70px;
`;
export const Container = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1rem;
`;

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  height: 200px;
  margin-bottom: 0.5rem;
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 5px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 35px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 4px rgba(3, 3, 3, 0.2);
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 0.5rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;

  &:nth-last-child(1) {
  }
`;

const BodyPanel = styled.div`
  width: 100%;
  overflow-x: scroll;
  padding-bottom: 1rem;
  border: 1px solid rgba(3, 3, 3, 0.3);
`;
